function delay(times) {
    return new Promise(resolve => {
        setTimeout(() => {
            resolve()
        }, times)
    })
}

import 'nprogress/nprogress.css'
import { start, done, configure } from 'nprogress'

configure({
    trickleSpeed: 20, //时间
    showSpinner: false, //是否显示圆圈
})

start() //开始
done() //结束
function getPageComponent(page) { //该函数返回一个Promise
    return async() => {
        start();
        if (process.env.NODE_ENV !== 'production') { //不是生产阶段就等待
            await delay(2000);
        }
        const component = await page();
        done();
        return component
    }
}
const routes = [{
        name: 'Home',
        path: '/',
        component: getPageComponent(() =>
            import ('@/views/Home')),
        meta: { title: '主页', }
    },
    {
        name: 'About',
        path: '/about',
        component: getPageComponent(() =>
            import ('@/views/About')),
        meta: { title: '关于我', }
    },
    {
        name: 'Demo',
        path: '/project/demo/:url',
        component: getPageComponent(() =>
            import ('@/views/Project/Demo')),
        meta: { title: '项目和效果详情', }
    },
    {
        name: 'Project',
        path: '/project',
        component: getPageComponent(() =>
            import ('@/views/Project')),
        meta: { title: '项目和效果', }
    },
    {
        name: 'Blog',
        path: '/article',
        component: getPageComponent(() =>
            import ('@/views/Blog')),
        meta: { title: '文章', }
    },
    //动态路由
    {
        name: 'CategoryBlog',
        path: '/article/cate/:categoryId',
        component: getPageComponent(() =>
            import ('@/views/Blog')),
        meta: { title: '文章分类', }
    },
    {
        name: 'Blogdetails',
        path: '/article/:detailId',
        component: getPageComponent(() =>
            import ('@/views/Blog/components/Details')),
        meta: { title: '文章详情', }
    },

    {
        name: 'Message',
        path: '/message',
        component: getPageComponent(() =>
            import ('@/views/Message')),
        meta: { title: '留言板', }
    },
]
export default routes;